:root {
  --WHITE: #fff;
  --LIGHT-BKG: #fafafa;
  --DARK-BKG: #1e1823;
  --ACCENT: #fd9222;
  --ACCENT-LIGH: #fcf1e0;
  --DARK: #111;
}

* {
  margin: 0;
  padding: 0;
}

body {
  background-color: var(--LIGHT-BKG);
  color: var(--DARK);
  letter-spacing: -.28px;
  font-family: Montserrat, sans-serif;
  font-size: 14px;
  font-weight: 400;
  line-height: 1.28;
}

.list {
  list-style-type: none;
  display: flex;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.clear-style {
  appearance: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding: 0 16px;
}

@media screen and (min-width: 375px) {
  .container {
    max-width: 375px;
    padding: 0 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    max-width: 768px;
    padding: 0 32px;
  }
}

@media screen and (min-width: 1200px) {
  .container {
    max-width: 1200px;
  }
}

.link {
  cursor: pointer;
  text-decoration: none;
}

.accent {
  color: var(--ACCENT);
}

.section-title {
  letter-spacing: -.48px;
  font-size: 24px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.2;
}

.btn {
  cursor: pointer;
  outline: none;
  display: block;
}

.btn-45 {
  height: 45px;
  border-radius: 100px;
  padding: 0 28px;
}

.btn-41 {
  height: 41px;
  border-radius: 100px;
  padding: 0 28px;
}

.btn-33 {
  height: 33px;
  border-radius: 100px;
  padding: 0 20px;
}

.btn-29 {
  height: 29px;
  border-radius: 100px;
  padding: 0 16px;
}

.btn-24 {
  height: 24px;
  border-radius: 35px;
  padding: 0 16px;
}

.btn-accent {
  background-color: var(--ACCENT);
  border: 1px solid var(--ACCENT);
  color: var(--WHITE);
}

.btn-outln {
  border: 1px solid var(--WHITE);
  color: var(--WHITE);
  background-color: #0000;
}

.btn-outln-accent {
  border: 1px solid var(--ACCENT);
  color: var(--DARK);
  background-color: #0000;
}

.btn-other {
  background-color: var(--LIGHT-BKG);
  color: var(--DARK);
  border: 1px solid #1111111a;
}

.modal {
  width: 100%;
  height: 100%;
  background-color: #2e2f4266;
  position: fixed;
  top: 0;
  left: 0;
}































